import React, {Component, useEffect, useState} from "react";
import "./Page3.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLinkedin, faGithub,  } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope } from "@fortawesome/fontawesome-free-solid";
import * as ScrollMagic from "scrollmagic"

 const Page3 = () => {

    useEffect(() =>{
    const controller = new ScrollMagic.Controller();

    var scene2 = new ScrollMagic.Scene({
        triggerElement: ".contactmoi",
        triggerHook: 0.8,
        duration: "110%",
    })
    .setClassToggle(".contactmoi", "visible")
 
    .addTo(controller);

    var scene3 = new ScrollMagic.Scene({
        triggerElement: ".emailed",
        triggerHook: 0.8,
        duration: "110%",
    })
    .setClassToggle(".emailed", "visible")
 
    .addTo(controller);

    var scene4 = new ScrollMagic.Scene({
        triggerElement: ".linkedin",
        triggerHook: 0.8,
        duration: "110%",
    })
    .setClassToggle(".linkedin", "visible")
 
    .addTo(controller);

    var scene5 = new ScrollMagic.Scene({
        triggerElement: ".github",
        triggerHook: 0.8,
        duration: "110%",
    })
    .setClassToggle(".github", "visible")
 
    .addTo(controller);
})

        return (

            <div className="contact">

                <a id="contact" href="#projects #about #main1"></a>

                <div className="contactmoi">
                    <p>Get in touch.</p>
                </div>

                <div className="contacticons">
                    <a href="mailto:mattstokes94@yahoo.co.uk" target="_blank"><p className="emailed">mattstokes94@yahoo.co.uk</p></a><br/>
                    <a href="https://www.linkedin.com/in/matthew-stokes-077414b7/" target="_blank"><FontAwesomeIcon icon={faLinkedin} className="linkedin" size="5x" /></a><br/>
                    <a href="https://github.com/stokesy94" target="_blank"><FontAwesomeIcon icon={faGithub} className="github" size="5x" /></a>
                </div>

                    <footer className="footer">
                        <p>My Portfolio by Matthew Stokes</p>
                    </footer>

            </div>

        )
    }

export default Page3;