import React, { Component, useRef, useEffect } from "react";
import "./Page2.scss";
import logo from "../assets/logo.png";
import Quiz from "../assets/Quiz.jpg";
import Calc from "../assets/calc.jpg";
import star from "../assets/starida.jpg";
import PT from "../assets/pt.jpg";
import me from "../assets/me.jpg";
import CV from "../assets/CV.pdf";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFile } from "@fortawesome/fontawesome-free-solid";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import Compound from "../assets/compound.jpg";
import $ from "jquery";
import jquery_ui from "jquery-ui";
import * as ScrollMagic from "scrollmagic";

const Page2 = () => {
  useEffect(() => {
    $("#num1").click(function () {
      $(".info").toggleClass("info2", 1000, "linear");
      $("#num1").toggleClass("pichide", 1000, "linear");
    });
  });

  useEffect(() => {
    $("#num2").click(function () {
      $(".info3").toggleClass("info4", 1000, "linear");
      $("#num2").toggleClass("pichide", 1000, "linear");
    });
  });

  useEffect(() => {
    $("#num3").click(function () {
      $(".info5").toggleClass("info6", 1000, "linear");
      $("#num3").toggleClass("pichide", 1000, "linear");
    });
  });

  useEffect(() => {
    $("#num4").click(function () {
      $(".info7").toggleClass("info8", 1000, "linear");
      $("#num4").toggleClass("pichide", 1000, "linear");
    });
  });

  useEffect(() => {
    const controller = new ScrollMagic.Controller();

    var scene = new ScrollMagic.Scene({
      triggerElement: ".b1element",
      triggerHook: 0.8,
      duration: "110%",
    })
      .setClassToggle(".b1element", "visible")

      .addTo(controller);

    var scene1 = new ScrollMagic.Scene({
      triggerElement: ".b2a",
      triggerHook: 0.8,
      duration: "400%",
    })
      .setClassToggle(".b2a", "visible")

      .addTo(controller);
  });

  return (
    <div className="about">
      <a id="about" href="#projects #class #main1 #animateArrow "></a>

      <div className="b1">
        <div className="b1element">
          <p>
            I have a passion for creating <span>fast</span>,{" "}
            <span>responsive</span> and <span>dynamic</span> websites. I am
            proficient at HTML, CSS and JavaScript and have a good understanding
            of various different frameworks and tools used in modern web design.{" "}
            <span>Please take a look at some of my work.</span>
          </p>
        </div>

        <div className="b1element">
          <p>Have a look at my past experience:</p>
          <a href={CV} target="_blank">
            <FontAwesomeIcon icon={faFile} className="cv" />
          </a>
        </div>
      </div>

      <div className="b2">
        <div className="b2a">
          <h3>Starida App</h3>
          <div className="overlayc">
            <img src={star} id="num4" className="pic" />
            <div className="info7">
              <h2>Technologies?</h2>
              <ul>
                <li>React</li>
                <li>React-Router</li>
              </ul>
              <p>
                An app for a tour operator, using reusable card components and
                incorporating responsive design.
              </p>
            </div>
          </div>
          <div className="buttonc">
            <a href="https://github.com/stokesy94/Starida-App" target="_blank">
              <button className="buttons">View Code</button>
            </a>
            <a href="https://stokesy94.github.io/Starida-App/" target="_blank">
              <button className="buttons">View Live</button>
            </a>
          </div>
        </div>
        <div className="b2a">
          <h3>PT Website</h3>
          <div className="overlayc">
            <img src={PT} className="pic" id="num1" />
            <div className="info">
              <h2>Technologies?</h2>
              <ul>
                <li>Vanilla JS</li>
                <li>ScrollMagic</li>
                <li>CSS Grid</li>
              </ul>
              <p>
                My idea of a modern personal trainer website. Really enjoyed
                learning and using ScrollMagic to give the website a dynamic
                feel.
              </p>
            </div>
          </div>
          <div className="buttonc">
            <a
              href="https://github.com/stokesy94/personaltrainerswebsite"
              target="_blank"
            >
              <button className="buttons">View Code</button>
            </a>
            <a
              href="https://stokesy94.github.io/personaltrainerswebsite/"
              target="_blank"
            >
              <button className="buttons">View Live</button>
            </a>
          </div>
        </div>

        <div className="b2a">
          <h3>Quiz App</h3>
          <div className="overlayc">
            <img src={Quiz} id="num2" className="pic" />
            <div className="info3">
              <h2>Technologies?</h2>
              <ul>
                <li>Vanilla JS</li>
                <li>XMLHttpRequest</li>
              </ul>
              <p>
                A fun quiz application where you can create, save, load and play
                a quiz.{" "}
              </p>
            </div>
          </div>
          <div className="buttonc">
            <a
              href="https://github.com/stokesy94/quiz-application"
              target="_blank"
            >
              <button className="buttons">View Code</button>
            </a>
            <a
              href="https://stokesy94.github.io/quiz-application/"
              target="_blank"
            >
              <button className="buttons">View Live</button>
            </a>
          </div>
        </div>

        <div className="b2a">
          <h3>Compound interest Calculator</h3>
          <div className="overlayc">
            <img src={Compound} id="num3" className="pic" />
            <div className="info5">
              <h2>Technologies?</h2>
              <ul>
                <li>React</li>
              </ul>
              <p>
                Takes advantage of React's state in order to dynamically render
                new values as inputted by the user via the sliders.
              </p>
            </div>
          </div>
          <div className="buttonc">
            <a href="https://github.com/stokesy94/CompoundCalc" target="_blank">
              <button className="buttons">View Code</button>
            </a>
            <a href="https://stokesy94.github.io/CompoundCalc/" target="_blank">
              <button className="buttons">View Live</button>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Page2;
